import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import BreadCrumbs from '@components/breadcrumbs'
import { DynamicForm } from '@cc/form/DynamicForm'
import { RepeaterForm } from '@cc/form/RepeaterForm'
import { customerForm } from '@fixed/customers/formComposition/customerForm'
import { adressesForm } from '@fixed/customers/formComposition/adressesForm'
import { contactForm } from '@fixed/customers/formComposition/contactForm'
import { startLoadingAddresses } from '../setup/general/addressesTypes/redux/actions'

export const CustomerFormScreen = () => {
 
    const dispatch = useDispatch()
    const { formData } = useSelector(state => state.form)

    dispatch(startLoadingAddresses())
 
    return (
        <form >
            <BreadCrumbs breadCrumbTitle='Aadir Cliente' breadCrumbParent='Clientes' breadCrumbActive='Aadir Cliente' />
            <DynamicForm formCustom={ customerForm } />
            <RepeaterForm { ...adressesForm }/>
            <RepeaterForm { ...contactForm }/>
        </form>
    )
}
