import React from 'react'

export const TextArea = ({
    field_label,
    field_id,
    field_name,
    field_placeholder,
    field_value,
    field_className,
    block_className
}) => {
    return (
        <div  className={ `${block_className} mt-1` }>
            <label>{field_label}</label>
            <textarea 
                id={ field_id } 
                name={ field_name } 
                placeholder={ field_placeholder }
                className={ field_className }
                defaultValue={field_value}
            ></textarea>
        </div>
        
    )
}
