import axios from "axios"

export const getAddressesTypes = async () => {

    const { data:dataSnap } = await axios.post(`${process.env.REACT_APP_HOST_URI}/setup/general/addressesTypes/list`)
    const data = []

    dataSnap.forEach(element => {
        data.push({
            value: element.id, label: element.name 
        })
    })
    
    return data
}